import { List } from '@material-ui/core';
import _get from 'lodash.get';
import { TOffer } from '../../../../types';
import SubCard from '../../../../ui-component/cards/SubCard';
import TextItem from '../../components/TextItem';
import { formatCurrency, getSellerName } from '../../../../helpers';
import Accordion from '../../../../ui-component/extended/Accordion';

type Props = {
	offer: TOffer;
};

const PropertyInformation = ({ offer }: Props) => {
	const propertyInformation = [
		{
			id: 'generalInformation',
			title: 'General Information',
			defaultExpand: true,
			content: (
				<List>
					<TextItem<TOffer>
						data={offer}
						source='PropertyData.FullAddress'
						label='Property Address'
					/>
					<TextItem<TOffer>
						data={offer}
						source='PropertyData.OwnerNames'
						label='Sellers(DataTree)'
					/>
					<TextItem<TOffer>
						data={offer}
						getValue={row =>
							formatCurrency(_get(row, 'Property.ListingDetails.ListPrice'))
						}
						label='Listing Price'
					/>
				</List>
			),
		},
		{
			id: 'sellerInformation',
			title: 'Seller Information',
			content: (
				<List>
					<TextItem<TOffer>
						data={offer}
						getValue={getSellerName}
						label='Seller Name'
					/>
					<TextItem<TOffer>
						data={offer}
						source='Property.SellerInfo.Email'
						label='Seller Email'
					/>
					<TextItem<TOffer>
						data={offer}
						source='Property.SellerInfo.Phone'
						label='Seller Phone'
					/>
				</List>
			),
		},
	];
	return (
		<SubCard title='Property Information'>
			<Accordion data={propertyInformation} />
		</SubCard>
	);
};

export default PropertyInformation;
